@import url(https://fonts.googleapis.com/css2?family=Bakbak+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: 'Bakbak One', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:#161122;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Card{
    background-color: #161122;
    text-align: center;
    position: relative;
    padding: 3px;
    margin: 20px;
    border-radius: 7px;
    
    transition: all .2s ease-in-out;
    cursor: pointer;
}
.hiden{
    z-index: 99;
    background-color: red;
    text-align: center;
}
.Card:hover{
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.search{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 80px;
    margin: 0;
    background-color: #161122;
}
.search-input{
    padding: 1em;
    max-width: 600px;
    outline: none;
    
    border: 2px solid black;
   
    background: #decfff;
}
.search-input:hover,
.search-input:focus{
    background-color: lightblue;
}
button{
    outline: none;
    padding: 1em 2em;
    cursor: pointer;
    background: white;
    border: 2px solid black;
    border-radius: 50px;
}
button:hover,
button:focus{
    background: lightblue;
}
#style-2::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

#style-2::-webkit-scrollbar
{
	width: 12px;
	background-color: #F5F5F5;
}

#style-2::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #161122;
}

.modal{
    position: absolute;
    height: 500px;
    width: 500px;
    background: linear-gradient(to right, #0F2027, #203A43, #2C5364);
    z-index: 99;
    margin-left: auto;
    margin-right: auto;
    top: 20%;
    left: 0;
    right: 0;

   
   
}
.modal_content{
    position: relative;
}
.Heading{
    text-align: center;
    margin: 10px 0px;
    font-size: 1.5em;
    font-family: 'Roboto Mono', monospace
}
.close{
    cursor: pointer;
    display: inline-block;
    position: absolute;
    padding: 3px 10px;
    border: 1px solid black;
    color: black;
    background: white;
    border-radius: 50px;
    top: 0;
    margin-left: 10px;
    margin-top: 10px;
    font-size: 15px;
    transition: all .2s ease-in-out;
}
.close:hover{
    background: rgb(100, 100, 100);
    color: white
}
.profilePic{
    height: 200px;
}
.info-content{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
   
    grid-gap: 10px;
   
    gap: 10px;
}
.flex{
    display: inline-flex;
    width: 100%;
    justify-content: center;
}
.pic_border{
    display: inline-flex;
    justify-content: center;
    background: white;
    border: 2px solid black;
}
.Info{
    font-size: 1.5em;
    margin: 0;
    font-family: 'Roboto Mono', monospace
}

.main{
  
    position: relative;
}
